import React from 'react';
import './styles.css'
import homebg from "./homebg.mp4";

var video = document.getElementById("homebg");
var btn = document.getElementById("myBtn");

function myFunction() {
  if (video.paused) {
    video.play();
    btn.innerHTML = "Pause";
  } else {
    video.pause();
    btn.innerHTML = "Play";
  }
}

function Home() {
  return (
    <div class="container">
      <video autoPlay muted loop id="homebg">
        <source src={homebg} type="video/mp4" />
        This browser cannot display HTML5 video.
      </video>

      <button id="myBtn" onClick="myFunction()">Pause</button>

      <div class="overlay">
        In a lot of progress
      </div>
    </div>
  );
}

export default Home;