import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import Home from "./Home";
import './styles.css';
import breloom from "./breloom.png";
import github from "./github.png";

function App() {
  return (
    <div class="page">
      <a href="https://breloom.xyz/"><img src={breloom} id="home" /></a> <br />
      <a href="https://github.com/BreloomBoom"><img src={github} id="github" /></a>
      <Router>
        <div>
          <Switch>
            <Route path="/">
              <Home />
            </Route>
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default App;